___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --radius: 10px;
}

main {
    /* margin-top: 50px; */
    margin-bottom: 70px;
    padding: 40px;
    font-family: Playfair;
}

.card-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 30px;
}

.card {
    height: 500px;
    display: flex;
    flex-direction: column;
    box-shadow: 2px 2px 5px rgb(158, 158, 158);
    background-color: white;
    border-radius: var(--radius);
}

.card-image {
    height: 60%;
    width: auto;
    object-fit: cover;
    border-top-left-radius: var(--radius);
    border-top-right-radius: var(--radius);
}

.card-description-container {
    flex: 1;
    height: 60%;
    padding: 0px 20px;
}

.card-text {
    font-family: Roboto;
    margin: 0px;
}

.description-header {
    display: flex;
    justify-content: space-between;

    i {
        font-size: 30px;
        align-self: center;
    }
}`, "",{"version":3,"sources":["webpack://./src/style/main.css"],"names":[],"mappings":"AAAA;IACI,cAAc;AAClB;;AAEA;IACI,sBAAsB;IACtB,mBAAmB;IACnB,aAAa;IACb,qBAAqB;AACzB;;AAEA;IACI,aAAa;IACb,2DAA2D;IAC3D,SAAS;AACb;;AAEA;IACI,aAAa;IACb,aAAa;IACb,sBAAsB;IACtB,0CAA0C;IAC1C,uBAAuB;IACvB,4BAA4B;AAChC;;AAEA;IACI,WAAW;IACX,WAAW;IACX,iBAAiB;IACjB,qCAAqC;IACrC,sCAAsC;AAC1C;;AAEA;IACI,OAAO;IACP,WAAW;IACX,iBAAiB;AACrB;;AAEA;IACI,mBAAmB;IACnB,WAAW;AACf;;AAEA;IACI,aAAa;IACb,8BAA8B;;IAE9B;QACI,eAAe;QACf,kBAAkB;IACtB;AACJ","sourcesContent":[":root {\n    --radius: 10px;\n}\n\nmain {\n    /* margin-top: 50px; */\n    margin-bottom: 70px;\n    padding: 40px;\n    font-family: Playfair;\n}\n\n.card-container {\n    display: grid;\n    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));\n    gap: 30px;\n}\n\n.card {\n    height: 500px;\n    display: flex;\n    flex-direction: column;\n    box-shadow: 2px 2px 5px rgb(158, 158, 158);\n    background-color: white;\n    border-radius: var(--radius);\n}\n\n.card-image {\n    height: 60%;\n    width: auto;\n    object-fit: cover;\n    border-top-left-radius: var(--radius);\n    border-top-right-radius: var(--radius);\n}\n\n.card-description-container {\n    flex: 1;\n    height: 60%;\n    padding: 0px 20px;\n}\n\n.card-text {\n    font-family: Roboto;\n    margin: 0px;\n}\n\n.description-header {\n    display: flex;\n    justify-content: space-between;\n\n    i {\n        font-size: 30px;\n        align-self: center;\n    }\n}"],"sourceRoot":""}]);
