___CSS_LOADER_EXPORT___.push([module.id, `header {
    padding-top: 35px;
    position: static
}

.background-rectangle {
    background-color: var(--accent);
    height: 125px;
    width: 100%;
    position: absolute;
    z-index: -1;
    transform: translateY(-50px) rotate(-15deg) scale(2, 6);
}

.header-container {
    margin: 0px 50px;
    display: flex;
}

.portrait-container {
    position: relative;
}

.portrait-img {
    height: clamp(500px, 40vw, 650px); 
    width: auto;
    border: 7px solid rgb(21, 20, 22);
    box-shadow: 2px 2px 5px rgb(8, 8, 8);
}

.portrait-heading {
    position: absolute;
    bottom: 15px;
    right: 15px;
    font-size: 40px;
    color: white;
    margin-bottom: 0px;
}

.secondary-heading {
    font-size: 32px;
    margin-top: 0px;
}

.about-me-container {
    background-color: white;
    padding: 20px 30px 20px 20px;
    margin: 50px 0px;
    box-shadow: 2px 2px 5px rgb(59, 59, 59);
    display: flex;
    flex-direction: column;
    justify-content: center;

    p {
        font-size: clamp(20px, 1vw, 24px);
    }
}

.icon-container {
    display: flex;
    justify-content: end;
    gap: 20px;
    margin-top: 20px;
    font-size: 20px;
}

@media (max-width: 1000px) {    
    .header-container {
        display: flow-root;
    }

    .portrait-img {
        height: 450px;
        margin-right: 20px;
    }

    .portrait-container {
        float: left;
    }

    .about-me-container {
        margin: 150px 0px 0px 50px;
        display: block;
    }
    
    .portrait-heading {
        margin-top: 0px;
        transform: translate(190%, 50px);
        bottom: auto;
        right: auto;
    }
}

@media (max-width: 620px) {
    .header-container {
       display: flex;
       flex-direction: column;
       align-items: center;
    }

    .portrait-container {
       float: none;
       height: clamp(500px, 90vw, 600px);
    }

    .portrait-img {
       height: 100%;
       margin: 0px;
    }

    .portrait-heading {
        transform: translate(0px, 0px);
        bottom: 0px;
        right: 10px;
    }

    .about-me-container {
        margin: 50px 0px 0px 0px;
        box-shadow: none;
    }
    
    .icon-container {
        justify-content: center;
        gap: 12px;
    }
}`, "",{"version":3,"sources":["webpack://./src/style/header.css"],"names":[],"mappings":"AAAA;IACI,iBAAiB;IACjB;AACJ;;AAEA;IACI,+BAA+B;IAC/B,aAAa;IACb,WAAW;IACX,kBAAkB;IAClB,WAAW;IACX,uDAAuD;AAC3D;;AAEA;IACI,gBAAgB;IAChB,aAAa;AACjB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,iCAAiC;IACjC,WAAW;IACX,iCAAiC;IACjC,oCAAoC;AACxC;;AAEA;IACI,kBAAkB;IAClB,YAAY;IACZ,WAAW;IACX,eAAe;IACf,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,eAAe;IACf,eAAe;AACnB;;AAEA;IACI,uBAAuB;IACvB,4BAA4B;IAC5B,gBAAgB;IAChB,uCAAuC;IACvC,aAAa;IACb,sBAAsB;IACtB,uBAAuB;;IAEvB;QACI,iCAAiC;IACrC;AACJ;;AAEA;IACI,aAAa;IACb,oBAAoB;IACpB,SAAS;IACT,gBAAgB;IAChB,eAAe;AACnB;;AAEA;IACI;QACI,kBAAkB;IACtB;;IAEA;QACI,aAAa;QACb,kBAAkB;IACtB;;IAEA;QACI,WAAW;IACf;;IAEA;QACI,0BAA0B;QAC1B,cAAc;IAClB;;IAEA;QACI,eAAe;QACf,gCAAgC;QAChC,YAAY;QACZ,WAAW;IACf;AACJ;;AAEA;IACI;OACG,aAAa;OACb,sBAAsB;OACtB,mBAAmB;IACtB;;IAEA;OACG,WAAW;OACX,iCAAiC;IACpC;;IAEA;OACG,YAAY;OACZ,WAAW;IACd;;IAEA;QACI,8BAA8B;QAC9B,WAAW;QACX,WAAW;IACf;;IAEA;QACI,wBAAwB;QACxB,gBAAgB;IACpB;;IAEA;QACI,uBAAuB;QACvB,SAAS;IACb;AACJ","sourcesContent":["header {\n    padding-top: 35px;\n    position: static\n}\n\n.background-rectangle {\n    background-color: var(--accent);\n    height: 125px;\n    width: 100%;\n    position: absolute;\n    z-index: -1;\n    transform: translateY(-50px) rotate(-15deg) scale(2, 6);\n}\n\n.header-container {\n    margin: 0px 50px;\n    display: flex;\n}\n\n.portrait-container {\n    position: relative;\n}\n\n.portrait-img {\n    height: clamp(500px, 40vw, 650px); \n    width: auto;\n    border: 7px solid rgb(21, 20, 22);\n    box-shadow: 2px 2px 5px rgb(8, 8, 8);\n}\n\n.portrait-heading {\n    position: absolute;\n    bottom: 15px;\n    right: 15px;\n    font-size: 40px;\n    color: white;\n    margin-bottom: 0px;\n}\n\n.secondary-heading {\n    font-size: 32px;\n    margin-top: 0px;\n}\n\n.about-me-container {\n    background-color: white;\n    padding: 20px 30px 20px 20px;\n    margin: 50px 0px;\n    box-shadow: 2px 2px 5px rgb(59, 59, 59);\n    disp