___CSS_LOADER_EXPORT___.push([module.id, `footer {
    background-color: var(--accent);
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 50px;
    padding: 20px 40px;
}

.personal-details-container {
    color: white;
    font-size: 20px;
    display: flex;
    flex-direction: column;
    max-width: 500px;
    margin-bottom: 25px;
}

.footer-img {
    width: auto;
    height: clamp(200px, 50vw, 300px);
}

.phone-number,
.email {
    display: flex;
    align-items: center;
    gap: 20px;

    img {
        width: auto;
        height: 40px;
    }
}

@media (max-width: 800px) {
    footer {
        flex-direction: column;
        padding: 0px;
    }

    .personal-details-container {
        padding: 0px 25px;
    }

    .footer-img {
        width: 100%;
        height: auto;
    }
}`, "",{"version":3,"sources":["webpack://./src/style/footer.css"],"names":[],"mappings":"AAAA;IACI,+BAA+B;IAC/B,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,SAAS;IACT,kBAAkB;AACtB;;AAEA;IACI,YAAY;IACZ,eAAe;IACf,aAAa;IACb,sBAAsB;IACtB,gBAAgB;IAChB,mBAAmB;AACvB;;AAEA;IACI,WAAW;IACX,iCAAiC;AACrC;;AAEA;;IAEI,aAAa;IACb,mBAAmB;IACnB,SAAS;;IAET;QACI,WAAW;QACX,YAAY;IAChB;AACJ;;AAEA;IACI;QACI,sBAAsB;QACtB,YAAY;IAChB;;IAEA;QACI,iBAAiB;IACrB;;IAEA;QACI,WAAW;QACX,YAAY;IAChB;AACJ","sourcesContent":["footer {\n    background-color: var(--accent);\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    gap: 50px;\n    padding: 20px 40px;\n}\n\n.personal-details-container {\n    color: white;\n    font-size: 20px;\n    display: flex;\n    flex-direction: column;\n    max-width: 500px;\n    margin-bottom: 25px;\n}\n\n.footer-img {\n    width: auto;\n    height: clamp(200px, 50vw, 300px);\n}\n\n.phone-number,\n.email {\n    display: flex;\n    align-items: center;\n    gap: 20px;\n\n    img {\n        width: auto;\n        height: 40px;\n    }\n}\n\n@media (max-width: 800px) {\n    footer {\n        flex-direction: column;\n        padding: 0px;\n    }\n\n    .personal-details-container {\n        padding: 0px 25px;\n    }\n\n    .footer-img {\n        width: 100%;\n        height: auto;\n    }\n}"],"sourceRoot":""}]);
